@import url("https://fonts.googleapis.com/css?family=Roboto+Slab:400");
@import url("https://fonts.googleapis.com/css?family=Shadows+Into+Light");

::-moz-selection {
    color: #fff;
    background: #9FBE5A;
}

::selection {
    color: #fff;
    background: #9FBE5A;
}

body {
    width: 100vw;
    height: 100%;
    padding-bottom: 50px;
    overflow-x: hidden;
    display: flex;
    align-items: center;
    justify-content: center;
    background: linear-gradient(to bottom, #16a085, #f4d03f) center/cover no-repeat;
}

h1 {
    margin-bottom: 30px;
    /* padding: 0 20vw; */
    /* font: 400 24px "Roboto Slab", serif; */
    /* font-size: 24px; */
    text-align: center;
    color: #fff;
}

.form__contact {
    max-width: 600px;
    margin: 0 auto;
    border-left: 30px solid white;
    -o-border-image: url(data:image/svg+xml;base64,) 5% 100% repeat;
    border-image: url(data:image/svg+xml;base64,) 5% 100% repeat;
    border-image-width: 0px 0px 0px 30px;
    transform: translateY(100%);
    -webkit-animation: init 1s ease-in-out forwards;
    animation: init 1s ease-in-out forwards;
}

.form__contact fieldset {
    position: relative;
    margin: 0;
    padding: 30px 30px 40px 80px;
    border: none;
    border-radius: 0 20px 20px 0;
    font: 24px "Shadows Into Light", cursive;
    background: #fff linear-gradient(rgba(0, 0, 0, 0.1) 1px, transparent 0) 0 20px/100% 40px;
}

.form__contact fieldset:after {
    position: absolute;
    top: 0;
    left: 50px;
    content: "";
    height: 100%;
    width: 1px;
    border-left: double #E08183;
}

.form__contact p {
    margin: 0 0 40px 0;
    line-height: 40px;
    color: #333;
}

.form__contact span {
    position: relative;
    line-height: 40px;
    cursor: pointer;
}

.form__contact button {
    margin-top: 40px;
    float: right;
    border: none;
    font-family: "Shadows Into Light", cursive;
    color: #E08183;
    background: transparent;
    cursor: pointer;
    transition: transform 0.25s ease;
}

.form__contact button:hover {
    transform: translateX(10px);
}

.form__field {
    display: inline;
    color: #7DB665;
    outline: none;
    font: 22px 'Roboto Slab';
}

.form__field:empty {
    display: inline-block;
    color: #DDD;
}

.form__field:empty:before,
.form__field:empty:focus:before {
    content: attr(data-placeholder);
}

svg {
    display: none;
}

@-webkit-keyframes init {
    75% {
        transform: translateY(-20px);
    }

    100% {
        transform: translateY(0);
    }
}

@keyframes init {
    75% {
        transform: translateY(-20px);
    }

    100% {
        transform: translateY(0);
    }
}

@-webkit-keyframes do-blur {
    0% {
        filter: url(#blur4);
    }

    25% {
        filter: url(#blur3);
    }

    50% {
        filter: url(#blur2);
    }

    75% {
        filter: url(#blur1);
    }

    100% {
        filter: url(#blur0);
    }
}

@keyframes do-blur {
    0% {
        filter: url(#blur4);
    }

    25% {
        filter: url(#blur3);
    }

    50% {
        filter: url(#blur2);
    }

    75% {
        filter: url(#blur1);
    }

    100% {
        filter: url(#blur0);
    }
}

.type-toolbar {
    margin: 20px;
    text-align: center;
}

.type-toolbar input[type="radio"] {
    opacity: 0;
    position: fixed;
    width: 0;
}

.type-toolbar label {
    display: inline-block;
    background-color: #ddd;
    padding: 10px 20px;
    font-family: sans-serif, Arial;
    font-size: 16px;
    border: 2px solid #444;
    border-radius: 4px;
    margin: 10px;
    font: 20px "Shadows Into Light", cursive;
}

.type-toolbar label:hover {
    background-color: #dfd;
    cursor: pointer;
}

.type-toolbar input[type="radio"]:checked+label {
    background-color: #bfb;
    border-color: #4c4;
}
